.json-node-type {
  color: #7f7f7f;
}

.json-node-toggler, .json-node-stub-toggler {
  color: inherit;
  text-decoration: none;
}

.json-node-toggler:hover, .json-node-stub-toggler:hover {
  background: #e2e2e2;
}

.json-node-children, .json-node-root {
  padding-left: 1em;
}

.json-node-header mark {
  background-color: #c7c10080;
  padding: 0;
}

.json-node-header mark.highlight-active {
  background-color: #c7672e80;
}

.json-node-label {
  color: #184183;
}

.json-node-number .json-node-value {
  color: #00f;
}

.json-node-string .json-node-value {
  color: green;
}

.json-node-boolean .json-node-value {
  color: #95110f;
}

.json-node-null .json-node-value {
  color: #959310;
}

.json-node-number .json-node-type, .json-node-string .json-node-type, .json-node-boolean .json-node-type, .json-node-undefined .json-node-type, .json-node-null .json-node-type {
  display: none;
}

.json-node-accessor {
  position: relative;
}

.json-node-accessor:before {
  content: "▶";
  font-size: .6em;
  line-height: 1.6em;
  transition: transform .1s ease-out;
  position: absolute;
  right: .5em;
}

.json-node-open .json-node-accessor:before {
  transform: rotate(90deg);
}

.json-node-stub-toggler .json-node-label, .json-node-collapse {
  color: #7f7f7f;
}

.json-node-collapse {
  font-size: .8em;
}

@keyframes json-node-children-open {
  from {
    transform: scaleY(0);
  }

  to {
    transform: scaleY(1);
  }
}

.json-node-link {
  color: #7f7f7f;
  padding-left: .5em;
  font-size: .8em;
  text-decoration: none;
  display: none;
}

.json-node-link:hover {
  color: #000;
}

.json-node-header:hover .json-node-link {
  display: inline;
}
